.single-adventure,
.page-about {
  .content-area {
    grid-area: main;
  }

  h1 {
    color: white;
    font-size: 3rem;
  }

  .hero-image-header {
    width: 100%;
    height: 100vh;
  }

  .entry-content {
    display: flex;
    flex-direction: column;
    margin: 0;

    h2 {
      width: 50%;
      margin: 0 auto;
      text-align: center;
      position: static;
    }
    p {
      width: 60%;
      margin: 0 auto;
      text-align: left;
      padding: 20px;
    }
  }
}

// single adventure styling

.single-adventure {
  header {
    img {
      width: 100%;
    }
    .adventure-author,
    h2 {
      text-align: left;
      width: 60%;
      @include center-block;
      text-transform: uppercase;
    }
    h2 {
      font-size: 2.4rem;
      padding: 20px 20px 0 20px;
      margin-bottom: 0.5rem;
    }
    .adventure-author {
      padding: 0 20px;
      margin-top: 0;
    }
  }

  article {
    margin-bottom: 72px;
  }

  .entry-content {
    padding: 0;
    font-family: $font__secondary;

    p {
      padding-bottom: 0;
    }
  }

  .button-container {
    width: 60%;
    margin: 1.5rem 0 0 0;
    @include center-block;
    padding-left: 20px;

    button {
      padding: 12px 24px;
      background: white;
      text-align: center;
      border: 1px solid black;
      box-shadow: none;
      text-shadow: none;
      text-transform: uppercase;
      border-radius: 0;

      .fab {
        span {
          font-family: $font__headers;
        }
      }

      &:hover {
        background-color: #248a83;
        color: white;
        border: none;
        border: 1px solid #248a83;
      }
    }
  }
}
